.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.banner{
    background: rgba(0, 0, 0, 0.2)url(/static/media/banner.213981a3.jpg);
    background-blend-mode: darken;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}

.banner-text-part{

    background-color: #725229;
    border-radius: 15px;
    
}

.banner-text{
    color: white;
    text-align: left;
    margin: 90px 0px;
}

.banner-text h1{
    font-size: 50px;
    font-weight: 600;

}
.banner-text p{
    font-size: 20px;
    color: black;
}
.have-look-btn{

    background-color:#555555 ;
    color: white;
    padding: 30px;
    width: 250px;
    border: 1px solid black;
    border-radius: 10px;
}

.single-watch{
    border-radius: 15px;
  
       padding: 10px;
       box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
   
   }
   
   .watch-text{
       text-align: left;
       margin: 15px 15px;
   }
   
   .watch-image{
       border-radius: 15px;
       height: 250px;
       
   }

   .section-heading{
       margin:  25px 0px;
       text-transform: uppercase;
       color: var(--text);
   }
   .sub-heading{
       font-size: 15px;
       margin: -20px;
       margin-bottom: 30px;
   }

   .see-book-btn{

    background-color: #725229;
    padding: 10px;
    border:none;
    border-radius: 5px;
   }
.single-review{
    background-color: var(--side);
    text-align: left;
    padding: 10px;
    border-radius: 15px;
}
.review-grid{

    display: grid;
    grid-template-columns: repeat(3,1fr);
}
.special-img{

    width: 80%;
    height: 70%;
    border-radius: 20px;
}

.specialty-text-part{
    text-align: left;
}

